:root {
  --sm: 768px;
  --md: 992px;
  --container: 1200px;
  --maxWidthContainer: 700px;
  --theme: #66819a;
}

.full-screen {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

img {
  width: 100%;
}

body {
  background: #f1f1f1;
}

.container,
.max-width-container {
  max-width: var(--container);
  padding: 0 15px;
  width: 100%;
  margin: 0 auto;
}

.max-width-container {
  max-width: var(--maxWidthContainer);
  margin-bottom: 30px;
}

.title {
  padding-top: 30px;
}

input {
  width: 100%;
  padding: 10px;
  border-radius: 3px;
  border: 1px solid #d6d6d6;
  font-size: 18px;
}

.alert {
  font-weight: bold;
  color: #bfbfbf;
  font-size: 2rem;
}

.alert-error {
  color: #f78383;
}

.m-bottom-15 {
  margin-bottom: 15px;
}

.p-bottom-15 {
  padding-bottom: 15px;
}

.m-bottom-30 {
  margin-bottom: 30px;
}

.p-center {
  text-align: center;
}
.color-theme {
  color: var(--theme);
}

.fs-1 {
  font-size: 3rem;
}

.fs-2 {
  font-size: 2rem;
}

@media screen and (min-width: 768px) {
  .sm-column-counter-2 {
    column-count: 2;
    column-gap: 15px;
  }
}

@media screen and (min-width: 992px) {
  .md-column-counter-3 {
    column-count: 3;
    column-gap: 15px;
  }
}
